/* ==========================================================================
   Qode Wishlist for WooCommerce plugin styles
   ========================================================================== */

// Wishlist buttons
.qwfw-add-to-wishlist {
	
	&.qwfw-shortcode {
		
		gap: 2px;
		color: $first-main-color;
		
		.qwfw-m-icon {
			display: none;
		}
		
		.qwfw-m-text {
			font-size: 13px;
			text-transform: uppercase;
			letter-spacing: 2px;
			line-height: 22px;
			font-weight: 500;
			color: inherit;
		}
		
		&.qwfw--added {
			&:before {
				content: "\e089";
			}
		}
		
		&:before {
			content: "\e030";
			font-size: 12px;
			float: left;
			padding-right: 6px;
			line-height: 19px;
			color: inherit;
			position: relative;
			display: inline-block;
			vertical-align: top;
			font-family: 'ElegantIcons';
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			transition: opacity .2s;
		}
	}
}

.qode-pl-holder {
	.qwfw-add-to-wishlist-wrapper {
		display: inline-block;
		vertical-align: middle;
		width: 26px;
		height: 26px;
		margin: 0 !important;
		text-align: center;
		background-color: #555;
		
		.qwfw-add-to-wishlist {
			&.qwfw-shortcode {
				display: flex;
				align-items: center;
				justify-content: center;
				height: 100%;
				
				&:before {
					content: "\e089";
					font-size: 11px;
					color: #fff;
					padding: 0;
					transition: opacity .2s;
				}
				
				&.qwfw--added {
					&:before {
						content: "\4e";
					}
				}
				
				&:hover {
					&:before {
						opacity: 0.8;
					}
				}
			}
		}
	}
}

// Wishlist table
.qwfw-wishlist-table {

	&.qwfw-layout--table {

		.shop_table {
			font-size: var(--qwfw-wt-items-font-size, 100%);
			margin: 50px 0 100px;

			thead {
				background-color: transparent;
				
				tr {
					border: 1px solid #e6e6e6;
					
					th {
						padding: 13px 10px;
						font-size: 14px;
						line-height: 1.571428571428571em;
						text-transform: uppercase;
						letter-spacing: 1px;
						font-family: Raleway, sans-serif;
						font-weight: 600;
						color: #000;
						
						&.product-thumbnail {
							width: 100px;
						}
					}
				}
			}

			tbody {

				tr {
					background-color: var(--qwfw-wt-content-bg-color, transparent);
					border: 1px solid #e6e6e6;

					td {
						padding: 5px 10px;
						vertical-align: middle;
						text-align: left;

						&.product-remove {
							border: 1px solid #e6e6e6;
							width: 60px;
							
							.qwfw-e-item-inner {
								text-align: center;
							}

							a {
								color: #505050;

								.qwfw-e-remove-button-icon {

									svg {
										width: 10px;
										height: auto;
										
										line {
											stroke-width: 4px;
										}
									}
								}
							}
						}

						&.product-thumbnail {
							a, img {
								display: block;
							}
							
							img {
								width: 66px;
							}
						}

						&.product-name {
							text-align: left;
							
							.qwfw-e-item-category {
								display: none;
							}

							a {
								color: #888;
							}
						}

						&.product-price {
							font-size: 14px;

							ins {
								text-decoration: none;
							}
							
							span.amount {
								color: #888;
							}
						}

						&.product-stock-status span {
							color: #000 !important;
						}

						&.product-add-to-cart {
							.qwfw-e-item-inner {
								align-items: center;
							}
							
							.qwfw-e-add-to-cart {
								padding: 0 30px;
								margin: 0;
								line-height: 38px !important;
								height: 38px !important;
								box-sizing: content-box;
							}
						}
					}
				}
			}
		}
	}
}
