.woocommerce.single-product.qode-product-gallery-slider-on-mobile {
	.product {
		.images {
			.qode-product-gallery-slider {
				display: none;
				margin: 0;
			}
			
			@include ipad-portrait {
				.woocommerce-product-gallery__wrapper {
					width: 100%;
					margin: 0;
					
					> div {
						display: none;
						
						&.qode-product-gallery-slider {
							display: block;
						}
					}
				}
			}
		}
	}
}