//Block widget
$slider-handle-size: 15px;
$slider-rail-height: 4px;
$slider-bottom-margin: 21px;

@mixin qodefWooSliderHandle() {
    background-image: none;
    border: none;
    border-radius: 100%;
    background-color: silver;
    height: $slider-handle-size;
    margin: 0;
    outline: none;
    position: relative;
    top: -#{($slider-handle-size - $slider-rail-height) / 2};
    width: $slider-handle-size;
    
    &:hover {
        background-color: silver;
    }
}

@mixin qodefWooSliderHandleFocus() {
    background-color: silver;
}

.wp-block-woocommerce-filter-wrapper {
    .wc-block-price-slider {
        margin: 20px 0 0;
    }
    
    .wc-blocks-filter-wrapper {
        h1, h2, h3, h4, h5, h6 {
            margin: 0;
        }
    }
    
    .wc-block-components-price-slider__range-input-wrapper {
        box-shadow: none;
        height: $slider-rail-height;
        margin: calc((#{$slider-handle-size} / 2)) 0;
        width: 100%;
        
        &:before {
            background: #ebebeb;
            opacity: 1;
        }
    }
    
    .wc-block-components-price-slider__range-input-progress {
        height: $slider-rail-height;
        color: $first-main-color;
    }
    
    // can’t comma-separate these type of selectors
    .wc-block-components-price-slider__range-input {
        
        &::-webkit-slider-thumb {
            @include qodefWooSliderHandle();
        }
        
        &::-moz-range-thumb {
            @include qodefWooSliderHandle();
        }
        
        &::-ms-thumb {
            @include qodefWooSliderHandle();
        }
        
        &:focus {
            
            &::-webkit-slider-thumb {
                @include qodefWooSliderHandleFocus();
            }
            
            &::-moz-range-thumb {
                @include qodefWooSliderHandleFocus();
            }
            
            &::-ms-thumb {
                @include qodefWooSliderHandleFocus();
            }
        }
    }
    
    .wc-block-components-price-slider__controls,
    .wc-block-components-price-slider__range-text {
        margin: $slider-bottom-margin 0 0 0;
        display: inline-flex;
        justify-content: flex-start;
        color: #7f7f7f;
        font-weight: 700;
        
        input {
            margin: 0;
        }
        
        span {
            font-family: 'Raleway', sans-serif;
            font-weight: 700;
            font-size: 13px;
            line-height: 38px;
            text-transform: uppercase;
            color: #303030;
            letter-spacing: 1px;
            
            &:first-child {
                
                &:after {
                    content: '—';
                    margin: 0 4px;
                }
            }
        }
    }
    
