/* Content Slider - begin
========================================================================== */
.woocommerce.single-product.qode-product-single-wide-gallery{

	.product {
		 .images {

			 width: 63.55%;
			 margin-right: 2.9%;

			 .woocommerce-main-image {
				 display: inline-block;
				 vertical-align: middle;
				 float: left;
				 width: 50%;
				 box-sizing: border-box;
				 padding: 0 5px;

				 img {
					 vertical-align: middle;
				 }
			 }

			 .thumbnails {
				margin: 0;

				 a {
					 float: left;
					 width: 50%;
					 margin: 0 0 10px;
					 padding: 0 5px;
					 box-sizing: border-box;

					 img {
						 vertical-align: middle;
					 }
				 }
			 }

			 .woocommerce-product-gallery__image {
				 float: left;
				 width: 50%;
				 margin: 0 0 10px;
				 padding: 0 5px;
				 box-sizing: border-box;
			 }
		 }

		 .summary {
			 width: 33.55%;
			 padding-right: 65px;
			 box-sizing: border-box;

			 .qode-sp-separator.separator {
				 margin-top: 25px;
			 }

			 .product_meta {

				 > span {
					 font-weight: inherit;
					 font-size: inherit;
					 text-transform: inherit;
					 padding: 2px 0;
				 }
				 >.social_share_list_holder>span {
					color: #333;
					font-size: inherit;
					text-transform: inherit;
					font-weight: inherit;
					padding: 2px 0;
				  }
			 }

		 }

		 .qode-product-gallery-wide-related {
			 clear: both;
			 padding-top: 80px;
		 }

		 .related ul.products{
		 	margin-bottom: 105px;
		 }
	 }

}


/* Content Slider - end
========================================================================== */