ul.products {
    > .product{
        > .yith-wcqv-button{
            display: none !important;
        }
    }
}

%yith-wcqv-button-style {
    .qode-yith-wcqv-holder {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        background-color: #000;
        width: 26px;
        text-align: center;

        .yith-wcqv-button {
            color: #fff;

            &:before {
                content: "\e037";
                position: relative;
                display: inline-block;
                vertical-align: top;
                font-family: "dripicons-v2";
                font-size: 14px;
                line-height: inherit;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                @include qodeTransition(opacity .2s);
            }

            &:hover {
                &:before {
                    opacity: .8;
                }
            }


            div {
                background-size: 15px 15px !important;
            }
        }
    }
}

.qode-pl-holder .qode-pli-inner .qode-pli-text-inner,
.qode-plc-holder .qode-plc-item .qode-plc-text-inner,
ul.products > .product .qode-pl-inner .qode-pl-text-inner{
    @extend %yith-wcqv-button-style;
}