/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    position: absolute; //absolute centering over added item
    margin:0 !important; //absolute centering over added item
    -webkit-transform-origin:0 0;
    transform-origin:0 0;
    @include qodeTransform(translate(-50%,-50%));
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding: 0 21px;
    line-height: 35px;

    #yith-wcwl-message {
        font-size: 14px;
        font-weight: 400;
        letter-spacing: -0.14px;

        &:before {
            position: relative;
            font-family: 'ElegantIcons';
            content: "\4e";
            top: 2px;
            right: 7px;
        }
    }
 
    //vanishing effect
    &.qode-wishlist-vanish-out {
      @include qodeAnimation(qodeWishlistvanishOut 1s both);
    }

    @keyframes qodeWishlistvanishOut {
      0% {
        opacity: 1;
        @include qodeTransform(scale(1,1) translate(-50%,-50%));
        -webkit-filter: blur(0px);
        filter: blur(0px);
      }

      100% {
        opacity: 0;
        @include qodeTransform(scale(2,2) translate(-50%,-50%));
        -webkit-filter: blur(10px);
        filter: blur(10px);
      }
    }
}

.qode-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    background-color: #555555;
    width: 26px;
    text-align: center;
    backface-visibility: hidden;
    
    .yith-wcwl-icon {
        display: none;
    }
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: block;
        vertical-align: top;
        font-size: 0;
        line-height: inherit;
        color: #fff;

        &:after {
            content: "\e089";
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-family: 'ElegantIcons';
            font-size: 11px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            @include qodeTransition(opacity .2s);
        }

        // content: "\4e";

        &:not(.qode-adding-to-wishlist):hover {
            &:after {
                opacity: .8;
            }
        }

        &.qode-adding-to-wishlist {
            @include keyframes(qodeWishlistLoading) {
                0%, 100% {
                    opacity:1;
                }

                50% {
                    opacity: 0.2;
                }
            }

            &:after {
                @include qodeAnimation(qodeWishlistLoading 1s ease infinite);
            }
        }

        &:hover {
            color: #fff;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        &:after {
            content: "\4e";
        }
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    span {
        display: none;
    }

    a {

        &:after {
            color: #fff;
        }
    }
}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 15px 30px;
        margin: 50px 0 30px;
        line-height: 48px;
        list-style: none;
        background-color: transparent;
        border: 1px solid #000;

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 50px 0 100px;
        border-color: #e0dede;

        thead th{
            color: #000;
            border-color: #e0dede;
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid #e0dede;
                border-right: 1px solid #e0dede;

                &:last-child{
                    border-bottom: 1px solid #e0dede;
                    border-right: 1px solid #e0dede;
                }

                td {
                    padding: 5px 10px;
                    vertical-align: middle;

                    &.product-remove {
                        border-left: 1px solid #e0dede;
                        padding: 5px 10px;

                        a {
                            font-size: 0;

                            &:after {
                                content: "\f404";
                                display: inline-block;
                                vertical-align: middle;
                                speak: none;
                                color: inherit;
                                font-family: "Ionicons";
                                font-size: 25px;
                                font-style: normal;
                                font-weight: normal;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                line-height: 1;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                        }
                    }

                    &.product-thumbnail {

                        a, img {
                            display: block;
                        }
                    }

                    &.product-name {
                        a {
                            color: #888;
                        }

                        .yith-wcqv-button {
                            height: 38px;
                            line-height: 38px;
                            padding: 0 28px;
                            margin: 0;
                        }
                    }

                    &.product-price{
                        font-size: 14px;

                        ins{
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status span{
                        color: #000 !important;
                    }

                    &.product-add-to-cart {
                        text-align: center;

                        a {
                            display: inline-block !important;
                            padding: 0 30px;
                            margin: 0;
                            line-height: 38px;
                            height: 38px;

                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
        }
    }
}

.qode-single-product-summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        background-color: transparent;
        display: block;
        margin: 25px 0;
        text-align: left;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            vertical-align: middle;

            &.show{
                display: inline-block !important;
            }

            a {
                font-size: 13px;
                line-height: inherit;
                color: $first-main-color;
                text-transform: uppercase;
                letter-spacing: 2px;
                line-height: 22px;
                font-weight: 500;

                &:after {
                    content: "\e089";
                    font-size: 12px;
                    float: left;
                    padding-right: 6px;
                    line-height: 19px;
                    color: $first-main-color;
                }
            }
        }

        .yith-wcwl-add-button {
            a {
                &:after {
                    content: "\e030";
                }
            }
        }
    }
}

.qode-wishlist-widget-holder{
    display: inline-block;
    vertical-align: middle;

    a {
        color: $first-main-color;
        font-size: 13px;
        margin: 0 10px;

        .qode-wishlist-items-number {
            color: #000;
            font-size: 12px;
        }

        &:hover {
            color: #000;
        }
    }
}

header.light {
    .qode-wishlist-widget-holder{
        a {
            color: #fff;
            .qode-wishlist-items-number {
                color: #fff;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

header.dark {
    .qode-wishlist-widget-holder{
        a {
            color: #000;
            .qode-wishlist-items-number {
                color: #000;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */